@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Montserrat:wght@300&display=swap');

.skillcontainer{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 200px 0;
}

.skillcontainer__text {
    color: white;
    flex: 0.6;
    text-align: left;
    max-width: 500px;
    margin-bottom: 20px;
    
}

.skillcontainer__text > h2{
    font-size: 30px;
}

.skillimage {
    flex: 0.4;
    text-align: center;
    margin-right: 100px;
}

.skillimage > img{
    width: 600px;
    border-radius: 3px;
    object-fit: contain;
}

.skillcontainer__skillset >h5{
    font-family: 'Montserrat',sans-serif;
    font-weight: 300;
    padding-bottom: 30px;
    padding-top: 30px;
}

.skillcontainer__slider >.MuiLinearProgress-colorPrimary {
    background-color: lightslategray;
}

.skillcontainer__slider1 >span >.MuiLinearProgress-barColorPrimary {
    background-color: tomato;
}


.skillcontainer__slider2 >span >.MuiLinearProgress-barColorPrimary {
    background-color: lightgreen;
}

.skillcontainer__slider3 >span >.MuiLinearProgress-barColorPrimary {
    background-color: lightpink;
}

.skillcontainer__slider4 >span >.MuiLinearProgress-barColorPrimary {
    background-color: lightyellow;
}

