@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Montserrat:wght@300&display=swap');

.experience{
    display: flex;
    flex-direction: column;
    border: 2px dotted white;
    border-radius: 10px;
    width:200px;
    height: 120px;
    justify-content: center;
    align-items: center;
    background-color: black;
    overflow: hidden;
}
.experience > p{
    font-family: 'Times New Roman', Times, serif;
    font-size: 20px;
    letter-spacing: 1px;
}

.experience > p{
    font-family: 'Times New Roman', Times, serif;
    font-size: 14px;
    letter-spacing: 1px;
}

.expinfo{
    height: 300px;
    width: 80vw;
    display: flex;
    justify-content: space-evenly;
    text-align: center;
    align-items: center;
    background-color: rgb(56, 57, 58);
    background-size: cover;
    background-position: center;
}