.projects{
    margin: 15px;
    width: 250px;
    height: 170px;
    transition: transform 100ms ease-in;
}

.projects > img {
    width: 250px;
    height: 170px;
}

.projects:hover {
    color: white;
    background-color: orangered;
    transform: scale(1.05);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.project__content{
    padding: 10px;
}
.project__content >h4 {
    padding-bottom: 5px;
}

.project__content >p{
    font-size: 15px;
    padding-bottom: 5px;
    
}