@import url('https://fonts.googleapis.com/css2?family=Alata&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&display=swap');

.Header {

    display: flex;
    align-items: center;
    color: white;
    background-color: black;
    top: 0;
    position: sticky;
    z-index: 2000;

}

.Header__left {
    flex: 0.4;
}

.Header__left > h1 {

    text-align: center;
    font-family: "Alata",sans-serif;
    letter-spacing: 1px;
    padding: 20px 0;
}

.Header__left > h1 >span {
     
    color: orangered;
}

.Header__right {
    display: flex;
    flex: 0.6;
    justify-content: space-evenly;
    align-items: center;
}

.Header__right > a > h4 {
    font-size: 15px;
    cursor: pointer;
    transition: transform 200ms ease-in-out;
    font-family: 'Crimson Text',serif;
    padding: 20px 0;
    letter-spacing: 1px;
}

.Header__right > a > h4:hover {
    transform: scale(1.09);
    background-color: orangered;
    color: white;
    padding: 5px 20px;
    border-radius: 2px;
    letter-spacing: 1px;
    font-family: 'Crimson Text',serif;
    font-size: 15px;
    cursor: pointer;
}

.Header__right > h4{

    background-color: orangered;
    color: white;
    padding: 5px 20px;
    border-radius: 2px;
    font-size: 15px;
    cursor: pointer;
}